











































































































































.query-container {
    display: flex;

    .wrap {
        display: flex;
        width: 100%;

        .search {
            flex-grow: 1;
            margin-right: 10px;
            padding: 10px;
            outline: 0;
            font-size: 1rem;
            border: 1px solid #F0151E; }

        .date {
            margin-right: 10px;
            border-radius: 0;
            border: 1px solid #F0151E;
            outline: none;
            letter-spacing: 0.5px;
            font-weight: bold;
            font-size: 1rem;
            font-weight: 300; }

        .digi-box {
            margin-right: 10px;
            color: white;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: white;
            color: #F0151E;
            padding-left: 10px;
            border: 1px solid #F0151E;
            border-radius: 5px;
            letter-spacing: 0.5px;

            &:hover {
                cursor: pointer; }

            &.selected {
                background-color: #F0151E;
                color: white; }

            input {
                display: none; }

            .fakebox {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 40px;
                height: 100%;
                font-size: 1.2rem; } }

        .limit {
            display: block;
            margin-right: 10px;
            padding: 10px;
            border: 1px solid #F0151E;
            width: 50px;
            font-size: 1rem;

            @media screen and (max-width: 768px) {
                width: 60px; } }

        .go {
            border: none;
            background-color: #F0151E;
            color: white;
            width: 50px;
            letter-spacing: 2px;
            font-weight: bold;
            font-size: 1rem;
            outline: none;

            &:hover {
                cursor: pointer;
                background-color: darken(#F0151E, 10%); } } }

    .toggle {
        display: none; }

    @media screen and (max-width: 768px) {
        padding: 0;
        flex-direction: column;

        .wrap {
            display: none;
            flex-direction: column;

            .search, .date, .digi-box, .limit {
                margin-right: 0;
                margin-bottom: 10px;
                padding: 10px; }

            .limit {
                width: 100%; }

            .go {
                width: 100%;
                padding: 12px;
                border-radius: 5px; } }

        &.open {
            .wrap {
                display: flex; }

            .toggle {
                margin-top: 10px; } }

        .toggle {
            color: red;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 10px;
            font-size: 1.2rem;

            i {
                margin-left: 10px;
                margin-top: -2.5px;
                animation: dance 500ms ease-in-out infinite alternate-reverse forwards;

                @keyframes dance {
                    from {
                        transform: translateY(0); }
                    to {
                        transform: translateY(2.5px); } } } } } }

.cards-container {
    display: flex;

    @media screen and (min-width: 769px) {
        flex-wrap: wrap;
        justify-content: center;
        padding: 30px; }

    @media screen and (max-width: 768px) {
        flex-direction: column;
        align-items: center;
        padding: 10px; } }

.pagination-container {
    display: flex;
    justify-content: center;
    padding-bottom: 30px;

    @media screen and (max-width: 768px) {
        padding-top: 30px; }

    .btn {
        background-color: #F0151E;
        border: none;
        border-radius: 0;
        color: white;
        padding: 20px;
        outline: none;

        &:first-child {
            margin-right: 10px; }

        &:disabled {
            cursor: not-allowed;
            background-color: #ddd; }

        &:hover:not(:disabled) {
            cursor: pointer;
            background-color: darken(#F0151E, 10%); } } }

