








































































.comic-container {
    padding: 30px !important;

    .comic {
        display: flex;

        .thumb {
            border: 2px solid #F0151E;
            min-width: 300px;
            min-height: 450px; }

        .content {
            margin-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .inner {
                display: flex;
                flex-direction: column;

                .title {
                    margin: 0;
                    color: #F0151E;
                    font-size: 3rem;
                    line-height: 2.5rem;
                    margin-bottom: 15px; }

                .description {
                    margin: 0;
                    font-size: 1.5rem;
                    text-align: justify; }

                .date {
                    margin-top: 15px; } }

            .links {
                display: flex;

                .beck-btn, .comics-btn {
                    background-color: #F0151E;
                    border: 0;
                    color: white;
                    padding: 15px;
                    width: 150px;
                    font-weight: bold;
                    text-transform: uppercase;
                    font-size: 1rem;
                    outline: none;
                    cursor: pointer;
                    text-align: center;

                    &:hover {
                        cursor: pointer;
                        background-color: darken(#F0151E, 10%); } } } } }

    @media screen and (max-width: 768px) {
        .comic {
            flex-direction: column;
            align-items: center;

            .content {
                margin-left: 0;

                .inner {
                    margin-top: 20px;
                    width: 300px;

                    .title {
                        text-align: center; }

                    .date {
                        margin-top: 10px; } }

                .links {
                    margin-top: 20px;
                    flex-direction: column-reverse;

                    .beck-btn {
                        width: 100%; } } } } } }
