













































@import "@/assets/styles/app.sass";

.nav {
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: #F0151E;

    .back-btn {
        position: absolute;
        top: 0;
        display: block;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 10px;
        color: white;
        text-decoration: none; }

    .title {
        margin: 0;
        color: white;
        text-align: center;
        text-transform: uppercase;

        .link {
            color: inherit;
            text-decoration: none; } } }

.footer {
    padding-top: 10px;
    padding-bottom: 10px;
    display: block;
    text-align: center;

    .copyrights {
        display: inline-block;
        text-decoration: none;
        color: #F0151E;
        text-align: center; } }

