






































































































.query-container {
    display: flex;
    padding: 10px;

    .search {
        flex-grow: 1;
        margin-right: 10px;
        padding: 10px;
        outline: 0;
        font-size: 1rem;
        border: 1px solid #F0151E; }

    .limit {
        display: block;
        margin-right: 10px;
        padding: 10px;
        border: 1px solid #F0151E;
        width: 50px;
        font-size: 1rem;

        @media screen and (max-width: 768px) {
            width: 60px; } }

    .go {
        border: none;
        background-color: #F0151E;
        color: white;
        width: 50px;
        letter-spacing: 2px;
        font-weight: bold;
        font-size: 1rem;

        &:hover {
            cursor: pointer;
            background-color: darken(#F0151E, 10%); } } }

.cards-container {
    display: flex;

    @media screen and (min-width: 769px) {
        flex-wrap: wrap;
        justify-content: center;
        padding: 30px; }

    @media screen and (max-width: 768px) {
        flex-direction: column;
        align-items: center;
        padding: 10px; } }

.pagination-container {
    display: flex;
    justify-content: center;
    padding-bottom: 30px;

    @media screen and (max-width: 768px) {
        padding-top: 30px; }

    .btn {
        background-color: #F0151E;
        border: none;
        border-radius: 0;
        color: white;
        padding: 20px;
        outline: none;

        &:first-child {
            margin-right: 10px; }

        &:hover {
            cursor: pointer;
            background-color: darken(#F0151E, 10%); }

        &:disabled {
            cursor: not-allowed;
            background-color: #ddd; }

        &:hover:not(:disabled) {
            cursor: pointer;
            background-color: darken(#F0151E, 10%); } } }
